.tabs__controls {
  display: flex;
  margin: 0;
  padding: 0;

  list-style: none;

  @include vp-767 {
    justify-content: space-between;
  }
}

.tabs__control {
  position: relative;

  margin-right: 10px;
  padding: 3px 20px;

  font-weight: 700;
  font-size: 16px;
  line-height: 34px;
  color: $color-default-white;

  background-color: $color-black-transparent3;
  border: none;

  transition: background-color $trans-600, opacity $trans-600;

  @include vp-767 {
    flex-grow: 1;
    margin-right: 5px;
    padding: 0;

    font-size: 12px;
    line-height: 30px;

    &:last-child {
      margin-right: 0;
    }
  }

  &.is-active {
    color: $color-zambezi;
    background-color: $color-whisper;
  }

  &:hover {
    opacity: 0.7;
    cursor: pointer;
  }
}

.tabs__element {
  width: 100%;
  max-width: 733px;
  padding: 30px 20px;

  border: 1px solid $color-whisper;

  @include vp-1279 {
    max-width: 588px;
    padding: 15px 10px;
  }

  @include vp-767 {
    max-width: 100%;
    padding: 15px 5px;
  }

  &--description .tabs__element-title {
    margin-bottom: 10px;

    color: $color-alizarin-red;

    @include vp-1279 {
      font-size: 20px;
      line-height: 26px;
    }

    @include vp-1023 {
      font-size: 16px;
      line-height: 24px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 22px;
    }
  }
}
